class Book{
    constructor(name,type){
        this.name = name;
        this.type = type;
    }
    getBook(){
        document.write(`<p>${this.name} - ${this.type}</p>`)
    }
}

//Î≥∏Î∂Ä Îç∞Ïù¥ÌÑ∞ ÎßåÎì§Í∏∞
const myBook = [
    new Book('book1','all'),
    new Book('book2','all'),
    new Book('book3','all')
];

//ÏßÄÏó≠ ÎßåÎì§Í∏∞
class It extends Book{
    constructor(name,type,level){
        super(name,type);
        this.level = level;
    }
    getCall(){
        document.write(`<p>${this.name} - ${this.type} - ${this.level}</p>`)
    }
}

//It Îç∞Ïù¥ÌÑ∞
const myIt = [
    new It('html','Ïù∏ÌÑ∞ÎÑ∑', 1),
    new It('javascript','Í∞úÎ∞ú', 2),
    new It('react','Ïï±', 3)
]

for(let i in myBook){
    myBook[i].getBook();
}

for(let i in myIt){
    myIt[i].getCall();
}